export default {
  data(){
    return{
      value1: 0,
      value2: 'Login.vue',
      parms:{
        name:null,
        region:null,
        price:null,
        area:null,
        pageNo:1,
        rors:1
      },
      users:[],
      loading: false,   //是否处于加载状态
      finished: false,  //是否已加载完所有数据
      isLoading: false,   //是否处于下拉刷新状态
      totalpage:null,
      id:this.$route.query.id,
    }
  },
  created() {
    this.getmsg()
  },
  mounted(){
    let winHeight = document.documentElement.clientHeight                          //视口大小
    document.getElementById('list-content').style.height = (winHeight-20) +'px'  //调整上拉加载框高度
  },
  methods:{
    //删除
    deleteitem(item){
     this.$dialog.confirm({
        message: '是否删除！'
      }).then(() => {
       let formdata=new FormData()
       formdata.append("id",item.pk_house)
        this.$axios.post("/house/del",formdata).then((res)=>{
          if (res.data.code=="ok"){
            this.getmsg()
            this.$toast.success('删除成功');
            this.parms.pageNo=1
          } else {
            this.$toast.fail('删除失败，请稍后重试！');
          }
        })
      }).catch(() => {
        // on cancel
      });
    },
    changeoption(){
      this.users=[]
      this.parms.pageNo=1
      this.getmsg()
    },
    onLoad() {      //上拉加载
      setTimeout(() => {
        this.loading = false;
        if (this.parms.pageNo<this.totalpage){
          this.parms.pageNo++
          this.getmsg()
        } else {
          this.finished = true;
          this.$toast('已经加载完毕!');
        }
        //
      }, 500);
    },
    onRefresh() {       //下拉刷新
      setTimeout(() => {
        this.finished = false;
        this.isLoading = false;
        this.parms.pageNo=1
        this.users=[]
        this.getmsg()
      }, 500);
    },
    getmsg(){
      let formdata;
      formdata=new FormData()
      formdata.append("pageno",this.parms.pageNo)
      formdata.append("id",this.id)
      this.$axios.post("/house/oalist",formdata).then((res) => {
        console.log(res)
        if (res.data.code=="error"){
          this.$toast.fail(res.data.message);
        } else {
          this.totalpage=res.data.data.totalpage
          res.data.data.rows.forEach((item)=>{
            this.users.push(item)
          })
          this.users.forEach((item,index)=>{
            this.users[index].houseimg=item.houseimg.split(",")
          })
        }
      })
    }
  }
}



